import clsx from "clsx";
import React from "react";
// import {  };
import { ContestStatusProps, Status } from "./ContestStatus.types";
import "./ContestStatus.scss";

const ContestStatus: React.FC<ContestStatusProps> = ({ status, className }) => {
  const styling = clsx({
    statusindicator: true,
    "statusindicator--upcoming": status === Status.UPCOMING,
    "statusindicator--live": status === Status.LIVE,
    "statusindicator--ended": status === Status.ENDED,
  });

  return (
    <div className="c4conteststatus">
      <div
        className={`${styling} ${className}`}
        style={{
          width: "0.5rem",
          height: "0.5rem",
          borderRadius: "50%",
          background: "white",
        }}
      />
      Live
    </div>
  );
};

export default ContestStatus;
